.register-container,
.login-container,
.forgot-password-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  background-color: #f0f0f0;
}

/* Form Styles */
.register-form,
.login-form,
.forgot-password-form {
  background-color: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 100%;
  text-align: center;
}

.register-form h2,
.login-form h2,
.forgot-password-form h2 {
  margin-bottom: 20px;
  font-family: Arial, sans-serif;
  color: #333;
}

/* Input Fields */
.register-form input,
.login-form input,
.forgot-password-form input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

/* Buttons */
.register-form button,
.login-form button,
.forgot-password-form button {
  width: 100%;
  padding: 10px;
  background-color: #00bfa6;
  border: none;
  color: white;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 20px;
}

.register-form button:hover,
.login-form button:hover,
.forgot-password-form button:hover {
  background-color: #019f87;
}

/* Links */
.login-link,
.register-link,
.reset-link {
  margin-top: 20px;
}

.login-link a,
.register-link a {
  color: #007bff;
  text-decoration: none;
  text-decoration: underline;
}

.reset-link a {
  color: #ff0400;
  text-decoration: none;
  text-decoration: underline;
}

.login-link a:hover,
.register-link a:hover,
.forgot-password-link a:hover,
.reset-link a:hover {
  text-decoration: underline;
}

/* Error and Success Messages */
.error {
  color: #ff3333;
  /* Consolidated error styles */
  font-size: 14px;
  margin: -10px 0 10px;
  /* Shortened margin definition */
}

.success {
  color: #28a745;
  /* Consolidated success styles */
  font-size: 14px;
  margin: -10px 0 10px;
  /* Shortened margin definition */
}

/* Navbar Styles */
.logo-img {
  max-width: 18%;
  height: auto;
}

.custom-navbar {
  background-color: rgb(2, 115, 159);
  padding: 0 1.5rem 0 1.5rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.custom-navbar .navbar-brand {
  font-size: 36px;
  color: white;
  font-weight: bold;
}

.custom-navbar .navbar-nav .nav-link {
  color: white;
  font-size: 24px;
  transition: color 0.3s ease;
}

.custom-navbar .navbar-nav .nav-link:hover,
.custom-navbar .navbar-nav .nav-link.active {
  color: #31c27a;
  font-weight: bold;
}

/* Footer Styles */
.custom-footer {
  background-color: rgb(2, 115, 159);
  color: white;
  text-align: center;
  padding: 1.5rem 0;
  font-size: 18px;
}

/* Account Settings Layout */
.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 20px;
}

.profile-section {
  display: flex;
  flex-direction: column;
  /* Stack children vertically */
  align-items: center;
  /* Center children horizontally */
  text-align: center;
  /* Center text inside */
}

.profile-img {
  width: 250px;
  height: 250px;
  margin-bottom: 20px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease;
}

.profile-img:hover {
  transform: scale(1.05);
}

/* Hide the default file input */
.custom-file-input {
  visibility: hidden;
  width: 0;
  height: 0;
  position: absolute;
}

/* Style the label to look like a button */
.custom-file-label {
  display: inline-block;
  padding: 8px 15px;
  background-color: #0f8d1e;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
  transition: background-color 0.3s ease;
}

.custom-file-label:hover {
  background-color: #0056b3;
}

/* Style the container where selected file name will appear */
.file-name {
  margin-left: 15px;
  font-size: 1rem;
  color: #333;
}

/* Button Outline Styles */
.btn-outline-success {
  color: #00bfa6;
