/* HomePage.css */

/* CSS Variables for Consistent Theming */
:root {
  --primary-color: #28a745; /* Bootstrap Success */
  --secondary-color: #17a2b8; /* Bootstrap Info */
  --accent-color: #ffc107; /* Bootstrap Warning */
  --light-bg: #f9f9f9;
  --dark-text: #343a40;
  --font-family: "Roboto", sans-serif;
}

/* Global Styles */
body {
  font-family: var(--font-family);
  color: var(--dark-text);
}

/* Hero Section */

.hero-section h1 {
  font-size: 3rem;
  margin-top: 20px;
  font-weight: 700;
}

.hero-section img {
  max-width: 1920px;
  height: 600px;
  margin-bottom: 20px;
}

.hero-section .btn {
  padding: 10px 30px;
  font-size: 1.2rem;
  border-radius: 30px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.hero-section .btn:hover {
  background-color: #218838;
  transform: translateY(-3px);
}

/* Introduction Section */
.intro-section {
  background-color: #ffffff;
}

.intro-section h4 {
  margin-top: 15px;
  font-weight: 600;
}

.intro-section p {
  color: #6c757d;
}

.intro-section .fa-icon {
  transition: transform 0.3s ease, color 0.3s ease;
}

.intro-section .fa-icon:hover {
  transform: scale(1.1);
  color: var(--primary-color);
}

/* Categories Navbar Section */
.categories-navbar-section {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.category-link {
  color: var(--dark-text) !important;
  font-weight: 500;
  padding: 10px 20px;
  transition: color 0.3s ease, background-color 0.3s ease;
  border-radius: 30px;
  margin: 0 5px;
}

.category-link:hover,
.category-link.active {
  color: #fff !important;
  background-color: var(--primary-color);
}

.subcategory-tabs-section .nav-link {
  color: bold;
  transition: color 0.3s;
}

.subcategory-tabs-section .nav-link.active {
  font-weight: #007bff;
}

/* Products Section */
.products-section {
  background-color: #ffffff;
}

.search-bar-section {
  border-bottom: 1px solid #dee2e6;
}

.search-bar-section .form-control {
  border-radius: 25px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.search-bar-section .btn {
  border-radius: 25px;
}
.products-section h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 40px;
  position: relative;
}

.products-section h2::after {
  content: "";
  width: 60px;
  height: 4px;
  background-color: var(--primary-color);
  display: block;
  margin: 10px auto 0;
  border-radius: 2px;
}

.products-section .card {
  border: none;
  border-radius: 15px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.products-section .card:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.products-section .card-img-top {
  transition: transform 0.3s ease;
}

.products-section .card-img-top:hover {
  transform: scale(1.05);
}

.products-section .btn {
  background-color: var(--primary-color);
  border: none;
  transition: background-color 0.3s ease;
}

.products-section .btn:hover {
  background-color: #218838;
}

/* Special Products Section */
.special-products-section {
  background-color: var(--light-bg);
}

.special-products-section h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 30px;
}

.special-products-section img {
  max-width: 100%;
  height: auto;
  border-radius: 15px;
  margin-bottom: 20px;
}

.special-products-section p {
  color: #6c757d;
  margin-bottom: 20px;
}

.special-products-section .btn {
  padding: 10px 30px;
  font-size: 1rem;
  border-radius: 30px;
