}

.special-products-section .btn:hover {
  background-color: #218838;
  transform: translateY(-3px);
}

/* Testimonials Section */
.testimonials-section {
  background-color: #ffffff;
}

.testimonials-section h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 40px;
  position: relative;
}

.testimonials-section h2::after {
