}

.products-on-sale-section .btn {
  background-color: var(--primary-color);
  border: none;
  transition: background-color 0.3s ease;
}

.products-on-sale-section .btn:hover {
  background-color: #218838;
}
/* Responsive Design */
@media (max-width: 768px) {
  .profile-img {
    width: 120px;
    height: 120px;
    margin-bottom: 15px;
  }

  .btn-outline-success,
  .btn-primary,
  .btn-success {
    width: 100%;
    margin-top: 15px;
  }

  .col-md-6,
  .col-md-4 {
    width: 100%;
    margin-bottom: 20px;
  }

  input,
  select {
    width: 100%;
  }
}

/* Animations for Interactive Feel */
@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-section h1 {
    font-size: 2rem;
  }

  .intro-section .fa-icon {
    margin-bottom: 20px;
  }

  .categories-navbar-section .category-link {
    padding: 8px 15px;
    margin: 5px;
    font-size: 0.9rem;
  }

  .products-section h2,
  .testimonials-section h2,
  .featured-products-section h2,
  .products-on-sale-section h2 {
    font-size: 1.5rem;
  }

  .special-products-section h2,
  .testimonials-section h2 {
    text-align: center;
  }
}

/* Smooth Scroll for Modal (if used) */
.modal.fade .modal-dialog {
  transition: transform 0.3s ease-out;
  transform: translateY(-50px);
}

.modal.show .modal-dialog {
  transform: translateY(0);
}


/* Admin.css */

/* General Layout */
.admin-container {
  display: flex;
  height: 100vh;
}

/* Sidebar Styles */
.sidebar {
  width: 250px;
  background-color: #2f3640;
  padding: 20px;
  color: #fff;
}

.sidebar h2 {
  margin-bottom: 20px;
}

.sidebar nav ul {
  list-style: none;
  padding: 0;
}

.sidebar nav ul li {
  margin-bottom: 10px;
}

.sidebar nav ul li a {
  color: #fff;
  text-decoration: none;
}

/* Main Content Styles */
.main-content {
  flex-grow: 1;
  padding: 20px;
  background-color: #f5f6fa;
}

.header {
  margin-bottom: 20px;
}

.header h1 {
  margin: 0;
}

.header p {
  color: #7f8c8d;
}

/* Stats Overview */
.stats-overview {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.stat-box {
  background-color: #fff;
  padding: 20px;
  flex: 1;
  margin: 0 10px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.stat-box h3 {
  margin-bottom: 10px;
}

.stat-box p {
  font-size: 1.5rem;
  font-weight: bold;
}

/* Charts Section */
.charts {
  margin-bottom: 20px;
}

.chart-box {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.chart-placeholder {
  height: 300px;
  background-color: #eaeaea;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  color: #7f8c8d;
}

/* Recent Activity */
.recent-activity {
  margin-bottom: 20px;
}

.activity-box {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.activity-box h3 {
  margin-bottom: 10px;
}

.activity-box ul {
  list-style: none;
  padding: 0;
}

.activity-box ul li {
  margin-bottom: 5px;
  color: #2c3e50;
}
